*, :before, :after {
  box-sizing: border-box;
  letter-spacing: 0;
  cursor: default;
  margin: 0;
  padding: 0;
  text-decoration: none;
}

.absolute {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 16px;
  width: 100%;
  max-width: 90%;
  padding: 48px;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 50%);
}

@media (width >= 768px) {
  .absolute {
    max-width: 80%;
    padding: 24px;
  }
}

@media (width >= 1440px) {
  .absolute {
    max-width: 1280px;
    padding: 80px 48px;
  }
}

.accordion-wrapper {
  flex-direction: column;
  gap: 10px;
  display: flex;
}

.accordion {
  color: #212529;
  background-color: #fff6;
  border-radius: 20px;
  width: 100%;
  height: fit-content;
  padding: 50px;
  transition: all .3s linear;
}

.accordion__top {
  justify-content: space-between;
  align-items: center;
  font-size: 18.3px;
  font-weight: 700;
  line-height: 23.04px;
  display: flex;
}

@media (width >= 768px) {
  .accordion__top {
    font-size: 27.22px;
    line-height: 34.56px;
  }
}

.accordion__top img {
  cursor: pointer;
  width: 40px;
  height: 40px;
  transition: all .3s linear;
  transform: rotate(180deg);
}

.accordion__bottom {
  max-height: 0;
  padding-top: 0;
  font-size: 18.688px;
  font-weight: 500;
  line-height: 31.2px;
  transition: all .3s linear;
  overflow: hidden;
}

.accordion--active {
  background-color: #fff;
}

.accordion--active img {
  transform: rotate(0);
}

.accordion--active .accordion__bottom {
  max-height: 600px;
  padding-top: 23px;
}

.button {
  cursor: pointer;
  letter-spacing: 1px;
  text-align: center;
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: max-content;
  padding: 22px 36px;
  font-size: 19.5px;
  font-weight: 800;
  line-height: 31.2px;
  transition: all .3s linear;
  display: flex;
}

.button a {
  cursor: pointer;
  display: flex;
}

.button--dark {
  background-color: #323232;
  border: 1px solid #323232;
}

.button--dark a {
  color: #fff;
}

.button--dark:hover {
  background-color: #fff;
  box-shadow: 0 0 10px #212529;
}

.button--dark:hover a {
  color: #212529;
}

.button--dark:active {
  transform: scale(.95);
}

.button--white {
  background-color: #fff;
  border: 1px solid #fff;
}

.button--white a {
  color: #212529;
}

.button--white:hover {
  background-color: #212529;
  box-shadow: 0 0 10px #fff;
}

.button--white:hover a {
  color: #fff;
}

.button--white:active {
  transform: scale(.95);
}

.button--big {
  padding: 28px 80px;
}

@media (width >= 768px) {
  .button--big {
    padding: 28px 48px;
  }
}

